const CLIPS = [
  {
    end: 30,
    id: '0',
    name: 'Clip One',
    start: 10
  },
  {
    end: 45,
    id: '1',
    name: 'Clip Two',
    start: 31
  }
];

export default {
  Query: {
    hello(obj: any, { subject }: { subject: string }) {
      return `Hello, ${subject}! from Server`;
    },
    clips: () => CLIPS
  }
};



